import React, { Component } from 'react';
import { Row, Col, Form, Checkbox, Input, message, Menu, Icon, Radio } from 'antd';
import { DatePickerString, SelectAjax } from '../../../components/common/index';
import { Btn, BtnClear } from '../../../components/common/button';
import { getTrainTypes } from '../../../services/trainType';
import { getStudyFields } from '../../../services/StudyField';
import { udpateQueryParams, getQueryParams } from '../../../utils/urlUtil';

@Form.create({ name: 'SearchBarStudent' })
class SearchBarStudent extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  getSemester = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Hc k 1',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getLop = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Lp A',
        },
        {
          id: '2',
          label: 'Lp B',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getLanthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getLoaithu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Thu SV nhp hc',
        },
        {
          id: '2',
          label: 'Thu khc',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getHinhthucthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Hnh thc A',
        },
        {
          id: '2',
          label: 'Hnh thc B',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getNamhoc = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '2018-2019',
        },
        {
          id: '2',
          label: '2019-2020',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getDot = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getDoituonghocphi = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Ngoi ngn sch',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getKhoahoc = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Kha 2018',
        },
        {
          id: '2',
          label: 'Kha 2019',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  exportExcel = () => {
    message.success('Demo disable when table data is empty');
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  formItemLayout = 6;

  menu = () => (
    <Menu onClick={this.handleMenuClick}>
      <Menu.Item key="Danh sch chi tit bin lai">
        <Icon type="printer" />
        Danh sch chi tit bin lai
      </Menu.Item>
      <Menu.Item key="Danh sch chi tit bin lai phi thu">
        <Icon type="printer" />
        Danh sch chi tit bin lai phi thu
      </Menu.Item>
      <Menu.Item key="Tng hp theo ngy v ngi thu">
        <Icon type="printer" />
        Tng hp theo ngy v ngi thu
      </Menu.Item>
    </Menu>
  );

  handleMenuClick = e => {
    message.info('Click on '.concat(e.key));
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const danophoso = getFieldValue('danophoso');
    const trainTypeId = getFieldValue('trainTypeId');
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="H">
              {getFieldDecorator('trainTypeId', {})(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="CN">
              {getFieldDecorator('studyFieldId', {})(
                <SelectAjax
                  service={getStudyFields}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId }}
                  allowGetData
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="S BD">
              {getFieldDecorator('sobaodanh', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="H v tn">
              {getFieldDecorator('hovaten', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="M SV">
              {getFieldDecorator('masinhvien', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Ngy sinh">
              {getFieldDecorator('ngaysinh', {})(<DatePickerString placeholder="Ngy sinh" />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('danophoso', {
                valuePropName: 'checked',
                initialValue: danophoso,
              })(<Checkbox>Ch tm nhng sv  np h s, giy t bn QLSV</Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('loaiphieu', {})(
                <Radio.Group setFieldsValue={setFieldsValue}>
                  <Radio value="daphanlop"> phn lp</Radio>
                  <Radio value="chuaphanlop">Cha phn lp</Radio>
                </Radio.Group>
              )}
            </Form.Item>
          </Col>
          <Col span={6} style={{ float: 'right' }}>
            <div className="btn-group">
              <Btn htmlType="submit" title="Tm kim SV" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBarStudent;



